(function () {
  $("[data-nav='toggle']").on('click', function () {
    $('.tabs__navigation-list').toggle()
  })

  var selectHandler = function (e, tab) {
    if (tab.newTab.index() === 0) {
      $('.steps__nav-item').last().css('display', 'none');
      $('.steps__nav-item').first().fadeIn("slow");
    } else {
      $('.steps__nav-item').first().css('display', 'none');
      $('.steps__nav-item').last().fadeIn("slow");
    }
  },
    tabOptions = {
      beforeActivate: selectHandler
    };
  $("#tabs").tabs(tabOptions);
})();

(function () {
  $(".nextBtn").on('click', function () {
    const step = $(this).attr('data-step');

    $('.steps__point').each(function () {
      if ($(this).attr('data-step') === step) {
        $(this).addClass('done');
      }
    })
  })
})();

