(function() {
  // $("#profile-tabs").tabs();
  $("#tabs-min").tabs();

  $(".jobs__switch__item")
    .click(function(e) {
      e.preventDefault();
      $(".jobs__switch__item")
        .removeClass("active")
        .eq($(this).index())
        .addClass("active");
      $(".vacancies__list")
        .hide()
        .eq($(this).index())
        .fadeIn();
    })
    .eq(0)
    .addClass("active");
})();
